import template from './incrementation.html'
import controller from './incrementation.controller'

export default {
  template,
  controller,
  bindings: {
    label: '@?',
    description: '@?',
    name: '@?',
    onChange: '&?',
    checked: '<?',
    disabled: '<?'
  },
  transclude: {
    label: '?ouiCheckboxLabel',
    description: '?ouiCheckboxDescription'
  }
}



